import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { ReceiptA4Component } from 'src/app/sales/components/print/receipt/receipt-a4/receipt-a4.component';
import { ReceiptA5Component } from 'src/app/sales/components/print/receipt/receipt-a5/receipt-a5.component';
import { ReceiptK80Col4Component } from 'src/app/sales/components/print/receipt/receipt-k80-col4/receipt-k80-col4.component';
import { ReceiptK80Col3Component } from 'src/app/sales/components/print/receipt/receipt-k80-col3/receipt-k80-col3.component';
import { ReceiptK57Col3Component } from 'src/app/sales/components/print/receipt/receipt-k57-col3/receipt-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // biến lấy tên nhóm (form kg cho dùng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  data: any;
  check: boolean = false; // biến bắt load dữ liệu khi xóa hoặc cập nhật phiếu thu
  type_receipt: any;
  radioValue: any;
  detailReceiptForm: FormGroup;
  money_currency: any;
  list_customers: any = [];
  listPaymentBank: any = [];
  disabled = true;
  isDisabled: boolean = false; //biến cờ true khi phiếu ở trạng thái đã hủy
  receiptGroup: any = this.vhQuerySales.getlocalIncoming_groups()
  /**Giới hạn thời gian cho tạo bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < (new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000)) return true
    return false
  }
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhQuery: VhQuery,
    private vhAuth: VhAuth,
    private fncService:FunctionService
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
    this.type_receipt = this.data.bill_type;
    if (this.type_receipt == 25 || this.type_receipt == 27) {
      this.isDisabled = true;
    }
    if(this.data) this.initForm()
  }

  ngOnInit(): void {
    this.list_customers = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(customer => customer['_id'] != "id_retail" && customer['debt_enable'] == true);
  }

  ngAfterViewInit() {
     // tạo cleavejs cho input price
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }
 
  /** trở về trang danh sách phiếu chi
   * @example this.goBack()
   */
  goBack() {
    let data = { ...this.dataRestore.dataRestore }
    data.check = true
    this.router.navigate(['/sales/dashboard/management/cashflow/receipts'], {
      state: this.check ? data : this.dataRestore.dataRestore
    });
  }
 /**
   * lấy các giá trị của bill khởi tạo cho form
   * @example initForm()
   */
  initForm(){
    this.detailReceiptForm = new FormGroup({
      date: new FormControl(this.data.date),
      customer: new FormControl({
        value: this.data.bill_type == 11 || this.data.bill_type == 27 ? this.data.id_customer : this.data.name,
        disabled: this.isDisabled
      },
        Validators.compose([Validators.required])),
      total: new FormControl({ value: this.data.total, disabled: this.isDisabled },
        Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl({ value: this.data.content, disabled: this.isDisabled }),
      payment_type: new FormControl(this.data.payment_type),
      wallet: new FormControl(this.vhQuerySales.getlocalWallet(this.data['id_wallet'])),
      type_receipt: new FormControl(this.data.bill_type),
      id_group: new FormControl({ value: this.data.bill_type == 7 || this.data.bill_type == 25 ? this.data.id_group : null, disabled: this.isDisabled })
    });
    this.radioValue = this.data.payment_type.toString();
  }

   /**
 * bắt sk chọn ngày tạo bill và check validate hợp lệ
 * @param $event date
 * @example this.changeDate('2023-06-18T16:27:16.276Z')
 */
  changeDate(event) {
    this.vhQuerySales.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.detailReceiptForm.controls['date'].setValue(new Date())
        }
        else {
          this.updateBill({date: this.detailReceiptForm.value.date})
          this.data.date = event
        }
      })

  }

  /**Zorro modal */
  isVisible = false;
   /**
   * mở modal hiển thị xác nhận xóa phiếu thu
   * @param value : biến cờ check in hay ko
   * @example this.showModal(true)
   */
  showModal(): void {
    this.isVisible = true;
  }
  /**
   * bắt sk nhấn nút ok của modal
   * @example this.showModal(true)
   */
  handleOk(): void {
    this.isVisible = false;
    if (this.vhAuth.checkMyPermission("cashflow_cancel_receipt")) this.deleteReceipt();
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  /**
   * bắt sk nhấn nút hủy của modal
   * @example this.showModal(true)
   */
  handleCancel(): void {
    this.isVisible = false;
  }

   /**Xóa phiếu thu khỏi DB
   * @example this.deleteReceipt()
  */
  deleteReceipt() {
    this.vhComponent.showLoading("").then(() => {
      this.vhQuerySales
        .deleteBill(this.data['_id'])
        .then((res: any) => {
          this.check = true
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1000,
            (this.languageService.translate("Receipt has been deleted successfully")) + " " + this.data.bill_code, "success-toast"
          );
          setTimeout(() => {
            this.goBack();
          }, 200);
        }, err => { })
    })
  }

  /** 
   * bắt thay đổi input total, cập nhật lại total và payment trong bill
   * @example this.updateTotal()
  */
  updateTotal() {
    if (this.money_currency.getRawValue() != "")
      this.updateBill({ total: parseInt(this.money_currency.getRawValue()), payment: parseInt(this.money_currency.getRawValue()) })

  }

  
  /**Update bill trên DB
   * @param data : các trường cần cập nhật
   * @example this.updateBill({total : 100000})
   */
  updateBill(data) {
    this.vhQuerySales.getValidityDate(this.detailReceiptForm.value.date)
    .then((validitydate: any) => {
      if (validitydate) {
        this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
        this.initForm()
      }
      else {
        if (this.type_receipt == 11) data['id_customer'] = data['customer']
        else { data['name'] = data['customer'] }
        this.check = true
        data['payment_type'] = this.data.payment_type;
        data['total'] = parseInt(this.money_currency.getRawValue());
        data['payment'] = parseInt(this.money_currency.getRawValue());
        if (this.detailReceiptForm.valid) {
          this.vhQuerySales.updateBill(this.data['_id'], data)
            .then(res => {
            })
            .catch(err => console.error('update failed => ' + err))
        }
      }
    })

  }

   /** kiểm tra dữ liệu máy in và show component in tương ứng
   * @example this.showPrint() 
   */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.data['content'] = this.detailReceiptForm['value'].content
        this.data['customer'] = [11, 27].includes(this.type_receipt) ? this.vhQuerySales.getlocalCustomer(this.data['id_customer']) : this.detailReceiptForm['value'].customer
        this.data['employee'] = this.vhAuth.getlocalEmployee(this.data['id_employee'])
        this.data['total'] = parseInt(this.money_currency.getRawValue())
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
          billInfo: this.data,
          printer: printer ,
        }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { })
          })
      }
    }, (err) => {
      let name :any= 'Receive from receivables / others'
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/sales/dashboard/manage/printer','page_desktop_receipt'],{state : name}))
    })

  }
  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReceiptK57Col3Component
      case 'print_size_k80_3c': return ReceiptK80Col3Component
      case 'print_size_k80_4c': return ReceiptK80Col4Component
      case 'print_size_a5': return ReceiptA5Component
      case 'print_size_a4': return ReceiptA4Component
      default: return ReceiptA4Component
    }
  }
  /*----------Print------------------*/
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_receipt')
      if (printer  && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }


  /***********Thêm xóa sủa nhóm thu********** */
  isVisiblelist: boolean = false; // popup danh sách nhóm
  activeButton: boolean = false
  id_group_default: any = this.receiptGroup.length ? this.receiptGroup.filter(item => item.default == true) : []
  defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
  /**
   * tạo nhóm thu mới lên DB
   * @param nameGroup : tên nhóm thu
   * @example this.addGroup('Thu tiền bán ve chai')
   */
  addGroup(nameGroup): void {
    let index = this.receiptGroup.findIndex(item => item.default)
    this.activeButton = true
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQuerySales.addIncoming_group(value).then((res) => {
        this.receiptGroup = [...this.receiptGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Chưa nhập tên nhóm")
  }
  /**
   * cập nhật lại trường default của nhóm thu đã chọn là true và các nhóm thu khác là false
   * @param item : nhóm thu đã chọn
   * @example this.setDefault(incoming_group)
   */
  setDefault(item) {
    let index = this.receiptGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.receiptGroup[index].default = false;
      Promise.all([this.vhQuerySales.updateIncoming_group(item._id, item), this.vhQuerySales.updateIncoming_group(this.receiptGroup[index]._id, this.receiptGroup[index])])
        .then(() => { })
    } else {
      this.vhQuerySales.updateIncoming_group(item._id, item).then(() => { })
    }
  }
  /**
   * mở modal chọn nhóm thu 
   * @example this.openLisGroup()
   */
  openLisGroup() {
    this.id_group_default = this.receiptGroup.length ? this.receiptGroup.filter(item => item.default == true) : []
    this.defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
    this.isVisiblelist = true
  }
  /**
   * tắt modal chọn nhóm chi
   * @example this.handleCancellist()
   */
  handleCancellist() {
    this.isVisiblelist = false;
  }

  changeEdit: any = ''
   /**
   * gán _id của nhóm chi đã chọn vào biến changeEdit để update css ở giao diện
   * @param _id : _id của nhóm chi đã chọn
   */
  editGroup(_id) {
    this.changeEdit = _id
  }
  /**
  * cập nhật tên nhóm chi
  * @param item nhóm chi đã chọn
  * @example this.saveEditGroup(item)
  */
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQuerySales.updateIncoming_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }

  /**
   * xóa nhóm chi lên DB và local
   * @param _id : _id của nhóm chi đã chọn
   * @param index : vị trí của nhóm chi trong mảng local
   * @example this.deleteGroup('BDUWYHDIWUFRIWUHDE', 1)
   */
  deleteGroup(_id, index) {
    this.vhQuerySales.deleteIncoming_group(_id).then((bool) => {
      if (bool) this.receiptGroup.splice(index, 1)
      else this.vhComponent.alertMessageDesktop("error", "Không xóa được nhóm này")
    })
  }
}
